import React, {useEffect, useState} from "react";
import {useSelector} from "react-redux";
import Form from "../../Other/forms/Form";
import FormBlock from "../../Other/forms/FormBlock";
import FormInput from "../../Other/forms/FormInput";
import FormTextarea from "../../Other/forms/FormTextarea";
import FormRadio from "../../Other/forms/FormRadio";
import FormCheckbox from "../../Other/forms/FormCheckbox";

const Checkout = () => {
    const checkoutItems = useSelector((store) => store.basket.items);
    const [loading, setLoading] = useState(false);
    const [viewDelivery, setViewDelivery] = useState(false);
    const [productItems, setProductItems] = useState([]);
    const checkoutTotal = checkoutItems.reduce((partialSum, item) => partialSum + (item.price * item.qty), 0);

    const getCheckoutItems = async (items) => {
        setLoading(true);
        setProductItems([]);

        const dataItems = await Promise.all(items.map(async item => {
            try {
                const url = `https://api.punkapi.com/v2/beers/${item.id}`;
                const apiResponse = await fetch(url);
                const data = await apiResponse.json();
                const dataObject = {
                    id: data[0].id,
                    name: data[0].name,
                    price: data[0].ibu,
                    image_url: data[0].image_url,
                    qty: item.qty
                };

                return dataObject;
            } catch (err) {
                console.log(err);
            }
        }));

        setProductItems(dataItems);
        setLoading(false);
    };

    const getListItems = () => {
        return productItems.map(el => {
          return `- : ${el.name}; : ${el.price}$; : ${el.qty} .; : ${el.price * el.qty}$ \n`
      }).join('');
    };

    const RenderContent = () => {
        return (
            <div className="checkout">
                <Form
                    nameForm="checkout"
                    sendMessage={sendMessageCheckoutForm}
                    textBtn=" "
                >
                    <FormBlock classes="checkout-form__block">
                        <div className="checkout-form__block-title"> </div>
                        <FormRadio
                            num={1}
                            name="type_client"
                            value=" "
                            defaultChecked
                        >
                             
                        </FormRadio>
                        <FormRadio
                            num={2}
                            name="type_client"
                            value=" "
                        >
                             
                        </FormRadio>
                    </FormBlock>
                    <FormBlock classes="checkout-form__block">
                        <div className="checkout-form__block-title"> </div>
                        <FormRadio
                            num={1}
                            name="type_delivery"
                            value=""
                            defaultChecked
                            onChanges={() => setViewDelivery(false)}
                        ></FormRadio>
                        <FormRadio
                            num={2}
                            name="type_delivery"
                            value=" "
                            onChanges={() => setViewDelivery(true)}
                        > </FormRadio>
                        {viewDelivery && <FormTextarea
                            name="delivery_address"
                            required
                            rows={4}
                        >
                             
                        </FormTextarea>}
                    </FormBlock>
                    <FormBlock classes="checkout-form__block">
                        <div className="checkout-form__block-title"> </div>
                        <FormRadio
                            num={1}
                            name="type_payment"
                            value=" "
                            defaultChecked
                        > </FormRadio>
                        <FormRadio
                            num={2}
                            name="type_payment"
                            value=" "
                        > </FormRadio>
                    </FormBlock>
                    <FormBlock classes="checkout-form__block">
                        <div className="checkout-form__block-title"> </div>
                        <FormInput
                            name="name"
                        >
                             
                        </FormInput>
                        <FormInput
                            name="phone"
                            mask="+7 (999) 999-99-99"
                            required
                        >
                             
                        </FormInput>
                        <FormInput
                            name="email"
                            required
                        >
                             e-mail
                        </FormInput>
                    </FormBlock>
                    <div className="checkout-form__block checkout-form__block--total">: {checkoutTotal}$</div>
                    <FormBlock classes="checkout-form__block checkout-form__block--policy">
                        <FormCheckbox
                            num={1}
                            name="policy"
                            value=""
                            defaultChecked
                            required
                        >     </FormCheckbox>
                    </FormBlock>
                </Form>
            </div>
        )
    };

    const sendMessageCheckoutForm = (data) => {
        const msgTypeClient = data['type_client'] ? ` : ${data.type_client.value}` : '';
        const msgName = data.name ? `: ${data.name.value}` : '';
        const msgPhone = data.phone ? `: ${data.phone.value}` : '';
        const msgEmail = data.email ? `Email: ${data.email.value}` : '';
        const msgTypePayment = data['type_payment'] ? ` : ${data.type_payment.value}` : '';
        const msgTypeDelivery = data['type_delivery'] ? ` : ${data.type_delivery.value}` : '';
        const msgAdr = data.delivery_address ? ` : ${data.delivery_address.value}` : '';
        const msgProducts = productItems ? ` :\n\n${getListItems()}` : '';
        const msgTotal = ` : ${checkoutTotal}$`;

        const msgText = `
     

 " "
${msgTypeClient}
${msgName}
${msgPhone}
${msgEmail}
${msgTypePayment}
${msgTypeDelivery}
${msgAdr}
-----------
${msgProducts}
${msgTotal}
        `;

        console.log(msgText);
    };

    useEffect(() => {
        getCheckoutItems(checkoutItems)
    }, [checkoutItems.length]);

    return (
        <>
            <h1 className="page-title"> </h1>
            {RenderContent()}
        </>
    )
};

export default Checkout;

